/* request网络请求 */
import axios, { AxiosRequestConfig, AxiosResponse } from 'axios';
import { notification, message } from 'antd';
import { v4 as uuidv4 } from 'uuid';
import cookie from '@/utils/cookie';
import Ajv, { ErrorObject } from 'ajv';
import { APP_ID } from '@/utils/constant.js';
const ajv = new Ajv();
// @ts-ignore
const { configToken, domain, projectVerson, basePath, proxy } = process.CONFIG;

export const token = cookie.get(configToken);
export const getProxyUrl = (url: string) => `https://ucms.ifeng.com/platform/services/_api_/proxy?url=${ encodeURIComponent(url) }`;

interface ProxyConfig {
  target: string;
  value: string;
  key: string;
  headers?: { HOST: string};
}

const proxyMap: any = Object.fromEntries(
  Object.entries(proxy)
    .map(([proxyUrl, config]: [string, ProxyConfig]) => {
      let realurl = config?.target.replace(/\/$/, '') + proxyUrl;

      realurl = realurl?.replace(config.key, config.value || '');

      if (config?.headers?.HOST) {
        realurl = realurl.replace(/(\d+\.){3}\d+/, config.headers.HOST);
      }

      return [proxyUrl, realurl];
    })
);

const codeMessage: { [key: number]: string } = {
  200: '服务器成功返回请求的数据。',
  201: '新建或修改数据成功。',
  202: '一个请求已经进入后台排队（异步任务）。',
  204: '删除数据成功。',
  400: '发出的请求有错误，服务器没有进行新建或修改数据的操作。',
  401: '用户没有权限（令牌、用户名、密码错误）。',
  403: '用户得到授权，但是访问是被禁止的。',
  404: '发出的请求针对的是不存在的记录，服务器没有进行操作。',
  406: '请求的格式不可得。',
  410: '请求的资源被永久删除，且不会再得到的。',
  422: '当创建一个对象时，发生一个验证错误。',
  500: '服务器发生错误，请检查服务器。',
  502: '网关错误。',
  503: '服务不可用，服务器暂时过载或维护。',
  504: '网关超时。'
};

interface ErrorMsg extends Error {
  status?: number;
  response?: AxiosResponse;
}


const transformPathToUrl = (path: string) => {
  const prePath: string = Object.keys(proxyMap).find((item: string) => path.startsWith(item)) || '';
  const realPath = proxyMap[prePath] || prePath;
  const url = path.replace(prePath, realPath);

  return `${ url }${ url.includes('?') ? '&' : '?' }version=${ projectVerson }`;
};


const whitelistMessage = ['Request aborted'];

function updateError({ err, url, title }: { err: any; url: string; title: string}) {
  if (whitelistMessage.includes(err?.message)) return ;
  try {
    axios({
      url: `${ basePath }/api/warn/schema/save`,
      method: 'post',
      responseType: 'json',
      headers: {
        'content-type': 'application/json'
      },
      data: {
        namespace: 'platform',
        appname: 'wind-vave',
        url: location.href,
        title,
        type: 'api',
        message: JSON.stringify(err),
        error: [],
        data: {
          sourceUrl: transformPathToUrl(url),
          operator: localStorage.username,
        }
      },
      timeout: 10 * 60 * 1000
    });
  } catch (error) {
    console.error(error);
  }

}

function checkStatus(response: AxiosResponse, withoutCheckCode?: boolean, successCode?: number): AxiosResponse | void {
  const isResponseError = response.status < 200 || response.status > 300;
  const dataCode = response.data?.code;
  const isDataError = !withoutCheckCode && dataCode !== 0 && dataCode !== 200 && dataCode !== successCode;

  if (!isResponseError && !isDataError) {
    return response;
  }

  const { data }: AxiosResponse = response;
  const errorStatus = isResponseError ? response.status : data?.code;

  const errorText: any = isResponseError ? data?.message : (data?.msg || data?.message);
  const error: ErrorMsg = new Error(errorText || codeMessage[errorStatus]);

  error.status = errorStatus;
  error.response = response;

  throw error;
}


/**
 * @param { string } url: 请求地址
 * @param { object } option: 请求配置项
 * @param {bool} option.withoutError: 禁止抛错
 * @param {bool} option.withoutCheckCode: 禁止检查code
 */
export interface Options extends AxiosRequestConfig {
  body?: any;
  schema?: any;
  successCode?: number;
  withoutError?: boolean;
  withoutCheckCode?: boolean;
}


const checkSchema = (
  schema: { [propsName: string]: any } | string | boolean,
  data: any,
  requestOption: {
    url: string;
    body: any;
  }
) => {
  if (typeof data === 'object') {
    const valid = ajv.validate(schema, data);
    const path = requestOption?.url;

    if (!valid) {
      ajv.errors?.forEach((item: ErrorObject) => {
        const errorObj = {
          message: `${ item.keyword } OF ${ item.dataPath } ${ item.message } FROM '${ path }'`,
          data,
          option: requestOption?.body
        };
        const errorMessage = ` JSON SCHEMA ERROR : ${ JSON.stringify(errorObj, null, 2) } `;

        console.error(errorMessage);
        updateError({ err: errorObj, url: path, title: 'schema 错误', });
      });
    }
  }
};

// eslint-disable-next-line consistent-return
async function request<T>(
  url: string,
  requestOptions: Options = {},
): Promise<T | undefined> {
  const { withoutError, schema, body, withoutCheckCode, successCode, ...options } = requestOptions;

  try {
    const requestConfig: AxiosRequestConfig = {
      method: options?.method ?? 'GET',
      headers: {
        authorization: `Bearer ${ token }`,
        commentAppId: APP_ID,
        versionInfo: JSON.stringify({
          url: encodeURIComponent(transformPathToUrl(url)),
          id: uuidv4(),
        }),
        ...(options?.headers || {})
      },
      data: ['POST', 'PUT'].includes((options?.method || 'GET')) ? { body } : body,
      responseType: options?.responseType ?? 'json',
      validateStatus: (): boolean => true,
      timeout: 10 * 60 * 1000
    };

    if (
      options.method
      && ['POST', 'PUT', 'DELETE'].includes(options.method)
      && body && body instanceof FormData
    ) {
      requestConfig.transformRequest = (data: any) => data;
    }
    const response: AxiosResponse<any> = await axios({ url, ...requestConfig });

    // updateError({ url, err: 'test' });

    checkStatus(response, withoutCheckCode, successCode);

    if (schema) {
      checkSchema(schema, response.data, { url, body });
    }

    const { code, message: dataMessage } = response.data;
    
    // userRole、uploadImg 接口成功，code为0
    if (code !== 200 && !url.includes('userRole') && !url.includes('upload') && !url.includes('getFxbData')) {
      notification.error({
        message: dataMessage
      });
    }
    

    return response.data;
  } catch (err) {
    console.log('request error');
    
    const { status }: ErrorMsg = err;

    if (status === 401 || status === -2) {
      notification.destroy();
      notification.error({
        message: codeMessage['401']
      });
      // token过期，清空当前环境token
      cookie.remove(configToken, { domain });
      setTimeout((): void => {

        window.location.href = `${ process['CONFIG'].linkLoginUrl }?redirect=${ encodeURIComponent(window.location.href) }`;
      }, 1500);
    } else {
      // updateError({ url, err, title: '接口错误' });
    }
    if (!withoutError) {
      message.destroy();
      message.error(err?.response?.data?.msg || err.message);

      return Promise.reject(err);
    }
  }
}

export default request;
