/* eslint-disable*/
const HOUR_TIME = 60 * 60 * 1000;

function getCookie(name) {
  let result = '';
  // 去掉空格
  const trimCookie = document.cookie.replace(/\s+/g, '');
  // 拆分成数组
  const cookieArr = trimCookie.split(';');
  // 组装成对象
  const cookieObj = {};
  cookieArr.forEach(item => {
    const eachArr = item.split('=');
    const key = eachArr[0];
    const value = eachArr[1];
    cookieObj[key] = value;
  });
  
  if (name) {
    result = cookieObj[name];
  }

  return result;
}

/**
 * 设置 cookie
 *
 * @inner
 * @param {string} name
 * @param {string} value
 * @param {Object=} options
 */
function setCookie(name, value, options = {}) {
  let expires = options.expires;

  if (!isNaN(expires)) {
    const hours = expires;
    expires = new Date();
    expires.setTime(expires.getTime() + hours * HOUR_TIME);
  }

  let path = options.path;
  if (path == null) {
    // 保证网站全局可用
    path = '/';
  }

  const domain = options.domain;
  document.cookie = [
    encodeURIComponent(name),
    '=',
    encodeURIComponent(value),
    expires ? ';expires=' + expires.toUTCString() : '',
    ';path=' + path,
    domain ? ';domain=' + domain : '',
    options.secure ? ';secure' : '',
  ].join('');
}

function remove(key, options = {}) {
  if (key == null) {
    return;
  }
  options.expires = null;

  setCookie(key, '', options);
}

export default {
  set: setCookie,
  get: getCookie,
  remove: remove,
};
